    import {mapGetters, mapState} from 'vuex';

    export default {
        name: "app-good-shop-recommendation",

        props: {
            showGoods: {
                type: Boolean,
                default: function () {
                    return false;
                },
                required: false
            },
            cardStyle: {
                type: String,
                default: function () {
                    return '1';
                },
                required: false
            },
            type: {
                type: String,
                default() {
                   return 'mch';
                },
                required: false
            },
            backgroundColor: {
                type: String,
                default() {
                   return '#fff';
                },
                required: false
            },
            theme: {
                type: [String, Object],
                required: false
            },
            page_id: {
                type: Number,
                required: false
            },
            index: {
                type: Number,
                required: false
            },
            is_required: {
                type: Boolean,
                required: false
            },
            mch_list: {
                type: Array,
                required: false
            },
            coupon_req: {
                type: Boolean,
                required: false
            }
        },
        computed: {
            ...mapGetters('mallConfig',{
                getVideo: 'getVideo'
            }),
            ...mapState({
                platform: function(state) {
                    return state.gConfig.systemInfo.platform;
                }
            })
        },
	    methods: {
            jump(data) {
                this.$jump({
	                url: `/plugins/mch/shop/shop?mch_id=${data}`,
	                open_type: 'navigate',
                });
            },
            router_jump(data, id) {

                if (data.goodsWarehouse.video_url && this.getVideo == 1) {

                    uni.navigateTo({
                        url: `/pages/goods/video?goods_id=${data.id}&sign=mch`
                    });






                } else {
                    uni.navigateTo({
                        url: `/plugins/mch/goods/goods?id=${data.id}&mch_id=${id}`,
                    });
                }







            },
            loadData() {
                this.$request({
                    url: this.$api.index.extra,
                    data: {
                        type: this.page_id === 0 ?'mall' : 'diy',
                        key: 'mch',
                        page_id: this.page_id,
                        index: this.index,
                        longitude: 0,
                        latitude: 0
                    }
                }).then(e => {
                    this.list = e.data;
                    if (this.page_id === 0) {
                        let storage = this.$storage.getStorageSync('INDEX_MALL');
                        storage.home_pages[this.index].list = this.list;
                        this.$storage.setStorageSync('INDEX_MALL', storage);
                    }
                })
            }
	    },

        data() {
            return {
                list: []
            }
        },

        watch: {
            mch_list: {
                handler(data) {
                    if (this.coupon_req) {
                        this.list = data;
                    }
                },
                deep: true
            }
        },
        mounted() {
            if (this.coupon_req) {
                this.list = this.mch_list;
            } else {
                if (this.is_required) {
                    this.loadData();
                } else {
                    let storage = this.$storage.getStorageSync('INDEX_MALL');
                    this.list = storage.home_pages[this.index].list;
                }
            }
        },
    }
